/* stylelint-disable */

@mixin xxl {
  @media (min-width: (1536px)) {
    @content;
  }
}

@mixin xl {
  @media (min-width: (1280px)) {
    @content;
  }
}

@mixin lg {
  @media (min-width: (1024px)) {
    @content;
  }
}

@mixin md {
  @media (min-width: (768px)) {
    @content;
  }
}

@mixin sm {
  @media (min-width: (640px)) {
    @content;
  }
}

@mixin ssm {
  @media (min-width: (390px)) {
    @content;
  }
}
