:root {
  --font-family: "Muller", sans-serif;
  --content-width: 1536px;
  --container-offset: 20px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --light-brown: #D9D3C7;
  --brown-color: #9D9288;
  --dark-brown: #3D342F;
  --black-color: #000000;
  --light-gray: #efefef;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */ /* stylelint-disable */ /* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Muller";
  src: url("../fonts/../fonts/MullerRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
  color: inherit;
}

h1 {
  font-size: 2em;
  margin: 0;
}

h2 {
  font-size: 1.5em;
  margin: 0;
}

h3 {
  font-size: 1.2em;
  margin: 0;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}
@media (min-width: 640px) {
  .container {
    padding: 0 40px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.inline-block {
  display: inline-block;
}

.btn {
  background-color: var(--brown-color);
  color: var(--light-color);
  padding: 10px 20px;
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: var(--dark-brown);
  }
}
.btn:active {
  background-color: var(--light-brown);
  color: var(--dark-brown);
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  background-color: var(--light-color);
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  padding: 10px 15px;
}
@media (min-width: 768px) {
  .header__bottom-menu {
    max-width: 75%;
    float: left;
  }
}
.header__cart {
  display: none;
  width: 25%;
  position: relative;
  float: right;
  z-index: 20;
}
@media (min-width: 768px) {
  .header__cart {
    display: block;
  }
}
.header__cart:focus {
  outline: none;
}
@media (any-hover: hover) {
  .header__cart:hover .header__cart-menu {
    visibility: visible;
    opacity: 100;
    pointer-events: all;
  }
}
.header__cart-mini {
  padding-left: 10px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  border-bottom: 1px solid var(--dark-brown);
  padding: 10px 0;
  transition: background-color 0.3s;
}
.header__cart-menu {
  position: absolute;
  top: 100%;
  right: 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
  background-color: #efefef;
  z-index: 20;
}
.header__cart-link {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  flex: 1;
}
.header__cart-icon {
  stroke: var(--dark-brown);
}
@media (any-hover: hover) {
  .header__cart-icon:hover {
    stroke: var(--brown-color);
  }
}
.header__cart-quantity {
  border: 2px solid var(--dark-brown);
  border-radius: 50%;
  width: 28px;
  height: 28px;
  text-align: center;
  font-size: 12px;
  line-height: 1.5rem;
}
@media (any-hover: hover) {
  .header__cart-quantity:hover {
    border: 2px solid var(--brown-color);
    color: var(--brown-color);
  }
}
.header__cart-list {
  border-bottom: 1px solid var(--brown-color);
}
@media (any-hover: hover) {
  .header__cart-item:hover {
    background-color: var(--light-brown);
  }
}

.menu {
  position: fixed;
  left: 0;
  right: 0;
  top: var(--header-height);
  height: 100vh;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transform: translateX(105%);
  transition: transform 0.3s;
  z-index: 20;
}
@media (min-width: 768px) {
  .menu {
    position: static;
    visibility: visible;
    opacity: 100;
    pointer-events: all;
    transform: translateX(0);
    height: auto;
  }
}
.menu__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
@media (min-width: 768px) {
  .menu__list {
    flex-direction: row;
    gap: 10px;
  }
}
.menu__link {
  font-size: 24px;
  padding: 10px 5px;
}
@media (min-width: 768px) {
  .menu__link {
    font-size: 16px;
  }
}
.menu--active {
  visibility: visible;
  opacity: 100;
  pointer-events: all;
  transform: translateX(0);
  overflow-y: auto;
  background-color: var(--light-color);
}
.menu--active .sub-menu {
  flex: 1 1 100%;
}

.menu-item {
  transition: background-color 0.3s;
  width: 100%;
  text-align: center;
}
.menu-item:focus {
  outline: 1px solid var(--dark-brown);
}
@media (any-hover: hover) {
  .menu-item:hover {
    background-color: var(--light-brown);
  }
  .menu-item:hover .sub-menu {
    opacity: 100;
    visibility: visible;
    pointer-events: all;
    transform: translate(0, 0);
    height: auto;
  }
}
.menu-item:active {
  outline: none;
  background-color: var(--brown-color);
}
@media (min-width: 768px) {
  .menu-item {
    width: auto;
  }
}

.menu-item-has-children {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 5px;
}
.menu-item-has-children__icon {
  width: 16px;
  height: 16px;
  transition: transform 0.3s;
}

.sub-menu {
  height: 0;
  width: 100%;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease-in-out;
  transform: translate(0, 10%);
  background-color: var(--light-gray);
  z-index: 20;
  overflow: hidden;
}
.sub-menu .menu-item {
  padding: 10px 0;
}
@media (min-width: 768px) {
  .sub-menu {
    position: absolute;
    left: 0;
    top: 100%;
    min-width: 180px;
    padding-top: 10px;
  }
}

.search {
  display: block;
  position: relative;
  width: 100%;
  max-width: 25%;
  align-items: center;
  display: none;
}
@media (min-width: 768px) {
  .search {
    display: flex;
  }
}
.search__input {
  width: 100%;
  position: relative;
  height: 2.5rem;
  flex: 1 1 auto;
  padding-left: 2.5rem;
  line-height: normal;
  font-size: 0.875rem;
}
.search__btn {
  max-width: 20px;
  max-height: 20px;
  height: 50%;
  margin: auto;
  width: 100%;
  right: auto;
  bottom: 0;
  top: 0;
  display: block;
  position: absolute;
  left: 0.75rem;
}
.woocommerce-mini-cart-item {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 10px;
}

.remove-from-cart-button {
  padding-right: 10px;
}

.attachment-woocommerce-thumbnail {
  max-width: 3em;
}

.quantity {
  flex: 1 1 100%;
  padding-left: 32px;
}

.woocommerce-mini-cart__total {
  padding-left: 10px;
}
.woocommerce-mini-cart__buttons {
  padding-left: 10px;
}

.wp-element-button {
  display: inline-block;
  margin: 5px 0;
}

.burger {
  --burger-width: 30px;
  --burger-height: 20px;
  --burger-line-height: 2px;
  position: relative;
  border: none;
  padding: 0;
  width: var(--burger-width);
  height: var(--burger-height);
  color: #000;
  background-color: transparent;
  cursor: pointer;
}
.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger::before {
  top: 0;
}
.burger::after {
  top: calc(100% - var(--burger-line-height));
}
.burger__line {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: var(--burger-line-height);
  background-color: currentColor;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.burger--active::before {
  top: 50%;
  transform: rotate(45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active::after {
  top: 50%;
  transform: rotate(-45deg);
  transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
}
.burger--active .burger__line {
  transform: scale(0);
  transition: transform 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

._active .sub-menu, ._active .header__cart-menu {
  opacity: 100;
  visibility: visible;
  pointer-events: all;
  transform: translate(0, 0);
  overflow: auto;
  height: auto;
}

.rotate-180 {
  transform: rotate(180deg);
}

.hero {
  margin-top: var(--header-height);
  padding-bottom: 2rem;
}
@media (min-width: 640px) {
  .hero__container {
    display: flex;
    align-items: center;
    gap: 2rem;
  }
}
.hero__post {
  text-align: center;
}
.hero__title {
  padding: 1.5rem 0;
}
.orbit {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 12rem;
  height: 12rem;
  margin: 0 auto;
  margin-top: 40px;
  z-index: 10;
}
@media (min-width: 390px) {
  .orbit {
    width: 16rem;
    height: 16rem;
  }
}
@media (min-width: 640px) {
  .orbit {
    width: 24rem;
    height: 24rem;
  }
}
@media (min-width: 768px) {
  .orbit {
    margin-top: auto;
  }
}
.orbit__planet {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: lightgray;
  border-radius: 100%;
  width: 3.5rem;
  height: 3.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 5;
  transition: width 0.3s, height 0.3s;
}
.orbit__planet:focus {
  outline: 2px solid var(--dark-brown);
}
@media (any-hover: hover) {
  .orbit__planet:hover {
    width: 5rem;
    height: 5rem;
  }
  .orbit__planet:hover .orbit__planet-img {
    width: 2.5rem;
    height: 2.5rem;
  }
}
.orbit__planet:active {
  outline: none;
}
.orbit__planet-img {
  width: 2rem;
  height: 2rem;
  transition: width 0.3s;
}
.orbit__list {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  width: 14rem;
  height: 14rem;
}
@media (min-width: 768px) {
  .orbit__list {
    width: 18rem;
    height: 18rem;
  }
}
.orbit__item {
  position: absolute;
  left: 0;
  transform-origin: 6rem;
  cursor: pointer;
}
@media (min-width: 390px) {
  .orbit__item {
    transform-origin: 7rem;
  }
}
@media (min-width: 768px) {
  .orbit__item {
    transform-origin: 9rem;
  }
}
.orbit__link {
  font-size: 10px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  text-align: center;
  width: 3rem;
  height: 3rem;
  transition: width 0.3s, height 0.3s;
}
@media (any-hover: hover) {
  .orbit__link:hover {
    width: 3.25rem;
    height: 3.25rem;
  }
  .orbit__link:hover .orbit__link-icon {
    opacity: 0;
  }
  .orbit__link:hover .orbit__link-text {
    opacity: 100;
  }
}
.orbit__link-icon {
  width: 1.5rem;
  height: 1.5rem;
  transition: opacity 0.3s;
}
@media (min-width: 390px) {
  .orbit__link-icon {
    width: 2.25rem;
    height: 2.25rem;
  }
}
.orbit__link-text {
  position: absolute;
  opacity: 0;
  transition: opacity 0.3s;
}

.recent-products {
  padding-bottom: 4rem;
}
.recent-products__title {
  padding: 1.25rem 0;
  text-align: center;
  text-transform: uppercase;
}
.products {
  display: flex;
  flex-wrap: wrap;
}
.products__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1 1 100%;
}
@media (min-width: 390px) {
  .products__item {
    flex: 1 1 50%;
  }
}
@media (min-width: 768px) {
  .products__item {
    flex: 1 1 25%;
  }
}
.products__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
}
.products__title {
  padding: 0.5rem 0;
}
.products__btn {
  margin: 0 auto;
}

.onsale {
  text-transform: uppercase;
  color: indianred;
}

.price__old {
  text-decoration: line-through;
  font-size: 0.9rem;
  padding-right: 5px;
}
.footer {
  background-color: var(--light-gray);
}
.footer__container {
  display: grid;
}
@media (min-width: 1280px) {
  .footer__container {
    grid-template-columns: 1fr 3fr;
    column-gap: 50px;
    justify-content: center;
    align-items: center;
  }
}
.footer_feedback {
  padding: 10px 0;
}
.footer__subscribe {
  padding: 40px 0;
}
.footer__row {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.subscribe__title {
  margin: 22px 0 11px;
  text-align: center;
  text-transform: uppercase;
}
.subscribe__form {
  border-bottom: 2px solid var(--black-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.subscribe__text {
  border: none;
  background-color: var(--light-gray);
  flex: 1 1 auto;
  line-height: 2;
  font-size: 18px;
  outline: none;
  padding: 0 5px;
}
.subscribe__btn {
  border: none;
  background-image: url("../img/email.svg");
  background-repeat: no-repeat;
  width: 24px;
}

.block {
  width: calc(50% - 5px);
}
.block--wide {
  flex: 0 0 auto;
}
@media (min-width: 768px) {
  .block {
    width: calc(25% - 8px);
  }
}
@media (min-width: 1280px) {
  .block {
    width: 20%;
    padding-top: 40px;
  }
}
.block__title {
  padding: 10px 0;
}
.block__item {
  padding: 3px 0;
}
.block__link {
  display: flex;
  align-items: center;
}
.block__link--small {
  font-size: 14px;
}
@media (min-width: 390px) {
  .block__link--small {
    font-size: 16px;
  }
}

.for-center-list {
  display: flex;
  justify-content: center;
  align-items: center;
}